<template>
    <div class="slider" ref="slider">
        <div class="slider-group" ref="sliderGroup">
            <slot></slot>
        </div>
        <div class="dots" v-show="MiniDot">
            <span class="dot"
                  :class="{active: currentPageIndex === index }"
                  v-for="(item, index) in dots">
            </span>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import {addClass} from 'common/js/dom'
    import BScroll from 'better-scroll'

    export default {
        name: 'slider',
        props: {
            loop: { // 
                type: Boolean,
                default: true
            },
            autoPlay: { // 
                type: Boolean,
                default: true
            },
            interval: {
                type: Number,
                default: 4000
            },
            MiniDot: {
                type: Boolean,
                default: false
            },
            pageIndex: {
                type: Number,
                default: 0
            }
        },
        data() {
            return {
                dots: [],
                currentPageIndex: 0
            }
        },
        mounted() {
            //  slider 
            setTimeout(() => {
                this._setSliderWidth();
                this._initDots();
                this._initSlider();
                //  
                if (this.autoPlay) {
                    //  
                    this._play()
                }
            }, 20);

            // 
            window.addEventListener('resize', () => {
                // scroll 
                if (!this.slider || !this.slider.enabled) {
                    return
                }

                // 
                clearTimeout(this.resizeTimer);
                // 
                this.resizeTimer = setTimeout(() => {
                    //  scroll 
                    if (this.slider.isInTransition) {
                        // true  _onScrollEnd
                        this._onScrollEnd()
                    } else {
                        // false 
                        if (this.autoPlay) {
                            // 
                            this._play()
                        }
                    }
                    //  dom 
                    this.refresh()
                }, 60)
            })
        },
        activated() {
            //  scroll
            this.slider.enable();

            // 
            let pageIndex = this.slider.getCurrentPage().pageX;

            //   && 
            if (pageIndex > this.dots.length) {
                //    1
                pageIndex = pageIndex % this.dots.length
            }

            // 
            this.slider.goToPage(pageIndex, 0, 0);

            //  
            if (this.loop) {
                //   -1
                pageIndex -= 1
            }

            // py
            this.currentPageIndex = pageIndex;

            // 
            if (this.autoPlay) {
                this._play()
            }
        },
        deactivated() {
            //   scroll
            this.slider.disable();
            // 
            clearTimeout(this.timer)
        },
        beforeDestroy() {
            //  scroll 
            this.slider.disable();
            // 
            clearTimeout(this.timer)
        },
        methods: {
            refresh() {
                // init
                if (this.slider) {
                    this._setSliderWidth(true);
                    this.slider.refresh()
                }
            },
            _setSliderWidth(isResize) {
                //  
                this.children = this.$refs.sliderGroup.children;
                // 
                let width = 0;
                // slider 
                let sliderWidth = this.$refs.slider.clientWidth;
                // 
                for (let i = 0; i < this.children.length; i++) {
                    let child = this.children[i];
                    addClass(child, 'slider-item');

                    child.style.width = sliderWidth + 'px';
                    width += sliderWidth
                }
                //  
                if (this.loop && !isResize) {
                    // 
                    width += 2 * sliderWidth
                }
                // 
                this.$refs.sliderGroup.style.width = width + 'px'
            },
            _initSlider() {
                //  scroll 
                this.slider = new BScroll(this.$refs.slider, {
                    scrollX: true,
                    scrollY: false,
                    momentum: false,
                    snap: {
                        loop: this.loop,
                        threshold: 0.3,
                        speed: 400
                    }
                });

                // :  _onScrollEnd 
                this.slider.on('scrollEnd', this._onScrollEnd);

                //   
                this.slider.on('touchend', () => {
                    if (this.autoPlay) {
                        this._play()
                    }
                });

                //  
                this.slider.on('beforeScrollStart', () => {
                    if (this.autoPlay) {
                        clearTimeout(this.timer)
                    }
                })
            },
            _onScrollEnd() {
                //   
                let pageIndex = this.slider.getCurrentPage().pageX;
                //   
                if (this.loop) {
                    pageIndex -= 1
                }
                // 
                this.currentPageIndex = pageIndex;
                // 
                if (this.autoPlay) {
                    this._play()
                }
                this.$emit('scrollToEnd', this.currentPageIndex)
            },
            _initDots() {
                //  
                this.dots = new Array(this.children.length)
            },
            _play() {
                // 
                let pageIndex = this.slider.getCurrentPage().pageX + 1;
                // 
                clearTimeout(this.timer);
                // 
                this.timer = setTimeout(() => {
                    // 
                    this.slider.goToPage(pageIndex, 0, 400)
                }, this.interval)
            },
            _listMove(i) {
                this.slider.goToPage(i, 0, 2000);
            }
        },
        watch: {
            pageIndex: "_listMove"
        }
    }
</script>

<style scoped lang="stylus">
    @import "~common/stylus/variable"

    .slider
        min-height 1px
        .slider-group
            position relative
            overflow hidden
            white-space nowrap
            height 100%
            .slider-item
                float left
                box-sizing border-box
                overflow hidden
                text-align center
                height 100%
                a
                    display block
                    width 100%
                    overflow hidden
                    text-decoration none
                img
                    display block
                    width 100%
        .dots
            position absolute
            right 0
            left 0
            bottom 12px
            transform translateZ(1px)
            text-align center
            font-size 0
            .dot
                display inline-block
                margin 0 4px
                width 8px
                height 8px
                border-radius 50%
                background $color-text-l
                &.active
                    width 20px
                    border-radius 5px
                    background $color-text-ll
</style>



