// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import App from './App'
import router from './router'

// 
import MaskModal from 'base/mask/index'
import HeaderComponent from 'base/header/index' // m-header  search   t-header  title 
import TabsComponents from 'base/tabs/index'

Vue.use(MaskModal);
Vue.use(HeaderComponent);
Vue.use(TabsComponents);

//  element-ui 
import 'element-ui/lib/theme-chalk/index.css'

// 
import 'common/stylus/index.styl'

//  element-ui 
Vue.use(ElementUI);

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    render: h => h(App)
});



